// Import contexts.
import createCookieContext from './factories/contexts/cookies.js';
import removeCookieContext from './contexts/removeCookie.js';
import setCookieContext from './contexts/setCookie.js';
export default class DoarsCookies {
  /**
   * Create plugin instance.
   * @param {Doars} library Doars instance to add onto.
   * @param {Object} options The plugin options.
   */
  constructor(library, options = null) {
    // Clone options.
    options = Object.assign({}, options); // Create contexts.

    const cookieContext = createCookieContext(options); // Enable plugin when library is enabling.

    library.addEventListener('enabling', () => {
      library.addContexts(0, removeCookieContext, setCookieContext, cookieContext);
    }); // Disable plugin when library is disabling.

    library.addEventListener('disabling', () => {
      library.removeContexts(removeCookieContext, setCookieContext, cookieContext);
    });
  }

}